// switcher style
.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 30px;
  }
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--rts-button-1);
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }
  .slider:before {
    position: absolute;
    content: "";
    height: 20px;
    width: 20px;
    left: 5px;
    bottom: 5px;
    background-color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }
  input:checked + .slider {
    background-color: var(--rts-button-1);
  }
  
  input:checked + .slider:before {
    transform: translateX(30px);
  }

  .newsletter{
    position: relative;
    max-width: 310px;
    input{
      position: relative;
      padding: 20px 18px;
      border-radius: 10px;
      border: 0;
    }
    button{
      position: absolute;
      right: 0;
      padding: 10px;
      border-radius: 6px;
      font-size: 16px;
    }
  }