.rts__single__blog{
    padding: 18px 18px 30px 18px;
    border-radius: 10px;
    border: 1px solid rgba($color: #7D8087, $alpha: 0.3);
    position: relative;
    &::before{
        content: '';
        position: absolute;
        inset: 0;
        height: 100%;
        width: 100%;
        background: linear-gradient(228.34deg, #FEF2EB 0%, #F1F1F1 46.31%, #E4E0EA 100%);
        border-radius: inherit;
        z-index: -1;
        opacity: 0;
        transition: var(--transition);
    }
    &:hover{
        border-color: transparent;
        &::before{
            opacity: 1;
        }
        .readmore__btn{
            i{
                transform: rotate(0deg);
            }
        }
    }
    .blog__img{
        img{
            border-radius: 10px;
            min-height: 240px;
            object-fit: cover;
            transition: var(--transition);
            transform: scale(1);
            overflow: hidden;
        }
    }
}
.blog__meta{
    padding: 12px 12px 0 12px;
    &__info{
        span, a{
            font-size: 16px;
            color: var(--rts-para);
        }
    }
}
.readmore__btn{
    color: var(--rts-heading);
    font-weight: 500;
    i{
        color: var(--rts-primary);
        transform: rotate(-35deg);
        transition: var(--transition);
    }
}